import React from 'react';
import * as _ from 'lodash';
import flow from 'lodash/fp/flow';
import filter from 'lodash/fp/filter';
import map from 'lodash/fp/map';

import Icon from 'src/components/Icon/Icon';
import Link from 'src/components/Link/Link';
import { enumColumnName } from 'src/constants/enums/preference';

import * as time from 'src/utils/time';

import { icons } from '../../constants/enums';
import * as columnType from '../../constants/enums/columnType';
import * as visitorStatus from '../../constants/enums/visitorStatus';
import * as visitorAudioVideoChatStatus from '../../constants/enums/visitorAudioVideoChatStatus';
import * as visitorStatusLanguage from '../../constants/languages/visitorStatus';
import TimeCell from './TimeCell';
import VisitorInfoCell from './VisitorInfoCell';
import Referrer from './Referrer';
import style from './style.css';

const baseCellGen = mapF => props => <span>{mapF(props.visitor, props)}</span>; // eslint-disable-line

const simpleCellGen = key => baseCellGen(visitor => visitor.get(key));

const linkCellGen = (linkKey, titleKey) => ({ visitor }) => ( // eslint-disable-line
  <Link
    href={visitor.get(linkKey)}
    data-tip={visitor.get(linkKey)}
    data-for="visitorListTooltip"
    newWindow
  >
    {visitor.get(titleKey) || visitor.get(linkKey)}
  </Link>
);

const referrerGen = ({ visitor }) => ( // eslint-disable-line
  <Referrer
    referrer={visitor.get('referrer')}
    searchEngine={visitor.get('searchEngine')}
    searchEngineKeywords={visitor.get('searchEngineKeywords')}
  />
);

const timeCellGen = key => ({ visitor, delay }) => // eslint-disable-line
  <TimeCell time={time.parseDate(visitor.get(key))} delay={delay} />;

const dateCellGen = key => baseCellGen((visitor, { dateTimeFormat, timezone, timezoneOffset }) => {
  /**
   * @type {Date}
   */
  const date = time.parseDate(visitor.get(key));
  if (!date) return '';
  return time.dateToString(date, dateTimeFormat, timezone, timezoneOffset);
});

const statusIconMapping = {
  [visitorStatus.waitingForChat]: {
    type: icons.waitting,
    text: visitorStatusLanguage.waitingForChat,
  },
  [visitorStatus.chatting]: {
    type: icons.chatting,
    text: visitorStatusLanguage.chatting,
  },
  [visitorStatus.preChat]: {
    type: icons.prechat,
    text: visitorStatusLanguage.preChat,
  },
  [visitorStatus.invited]: {
    type: icons.invited,
    text: visitorStatusLanguage.invited,
  },
  [visitorStatus.autoInvited]: {
    type: icons.invited,
    text: visitorStatusLanguage.autoInvited,
  },
  [visitorStatus.offlineMessage]: {
    type: icons.offline,
    text: visitorStatusLanguage.offlineMessage,
  },
  [visitorStatus.refusedByOperator]: {
    type: icons.refused,
    text: visitorStatusLanguage.refusedByAgent,
  },
  [visitorStatus.refusedByVisitor]: {
    type: icons.refused,
    text: visitorStatusLanguage.refusedByVisitor,
  },
  [visitorStatus.chatEnded]: {
    type: icons.chatEnd,
    text: visitorStatusLanguage.chatEnded,
  },
  [visitorStatus.inSite]: {
    type: icons.insite,
    text: visitorStatusLanguage.inSite,
  },
  [visitorStatus.outOfSite]: {
    type: icons.outOfSite,
    text: visitorStatusLanguage.outOfSite,
  },
  [visitorStatus.transferring]: {
    type: icons.transferring,
    text: visitorStatusLanguage.transferring,
  },
  [visitorStatus.manuallyInvitedAndWaitingVisitor]: {
    type: icons.inviteWaiting,
    text: visitorStatusLanguage.manuallyInvitedAndWaitingVisitor,
  },
  [visitorStatus.systemProcessing]: {
    type: icons.systemProcessing,
    text: visitorStatusLanguage.systemProcessing,
  },
  [visitorStatus.invitedAndWaitingForChat]: {
    type: icons.waitingInvite,
    text: visitorStatusLanguage.invitedAndWaitingForChat,
  },
  [visitorStatus.monitorChatEnded]: {
    type: icons.monitorEnded,
    text: visitorStatusLanguage.monitorChatEnded,
  },
  [visitorStatus.monitorChatting]: {
    type: icons.monitorChatting,
    text: visitorStatusLanguage.monitorChatting,
  },
};

const customVariableCellGen = ({ customVariables, visitor }, columnConfig) => { // eslint-disable-line
  const customVariable =
    _.find(customVariables, cv => cv.id === columnConfig.customVariableId);
  const values = visitor.get('customVariables');
  const value = customVariable ? _.find(values, v => v.name === customVariable.name) : '';
  if (value && value.url) {
    return (
      <Link
        newWindow
        href={value.url}
        data-tip={value.url}
        data-for="visitorListTooltip"
      >
        {value.value}
      </Link>
    );
  }
  return <span>{value ? value.value : ''}</span>;
};

const statusCell = ({ visitor }) => { // eslint-disable-line
  const status = visitor.get('status');
  let { type, text } = statusIconMapping[status];
  if (status === visitorStatus.waitingForChat && visitor.get('autoInvitationId') > 0) {
    type = icons.waitingInvite;
    text = visitorStatusLanguage.invitedAndWaitingForChat;
  } else if (status === visitorStatus.chatting) {
    const audioVideoChatStatus = visitor.get('audioVideoChatStatus');
    switch (audioVideoChatStatus) {
      case visitorAudioVideoChatStatus.visitorRequestAudioChat:
      case visitorAudioVideoChatStatus.agentRequestAudioChat:
        type = icons.audioChatRequesting;
        text = visitorStatusLanguage.audioChatRequesting;
        break;
      case visitorAudioVideoChatStatus.visitorRequestVideoChat:
      case visitorAudioVideoChatStatus.agentRequestVideoChat:
        type = icons.videoChatRequesting;
        text = visitorStatusLanguage.videoChatRequesting;
        break;
      case visitorAudioVideoChatStatus.audioChatting:
        type = icons.audioChatRequesting;
        text = visitorStatusLanguage.audioChatting;
        break;
      case visitorAudioVideoChatStatus.videoChatting:
        type = icons.videoChatRequesting;
        text = visitorStatusLanguage.videoChatting;
        break;
      default:
        break;
    }
  }
  return <Icon size="smaller" type={type} data-tip={text} data-for="visitorListTooltip" />;
};

const needSearchCellGen = (idKey, dictKey) => (props) => {
  const ids = [].concat(props.visitor.get(idKey)); // eslint-disable-line
  const name = flow(
    map(id => _.find(props[dictKey], prop => prop.id === id)),
    filter(element => element),
    map(element => element.name)
  )(ids).join(', ');
  return <span>{name}</span>;
};

const campaignCellGen = needSearchCellGen('codePlanId', 'campaigns');
const departmentCellGen = needSearchCellGen('departmentId', 'departments');
const autoInvitationCellGen = needSearchCellGen('autoInvitationId', 'autoInvitations');
const agentCellGen = needSearchCellGen('chatInvolvedAgents', 'agents');

const waitTimeCell = (props) => {
  const visitor = props.visitor; // eslint-disable-line
  const status = visitor.get('status');
  if (status === visitorStatus.waitingForChat) {
    return <TimeCell time={visitor.get('chatBeginTime')} />;
  } else if (status === visitorStatus.transferring) {
    return <TimeCell time={visitor.get('transferTime')} />;
  }
  return null;
};

const chatTimeCell = (props) => {
  const visitor = props.visitor; // eslint-disable-line
  const status = visitor.get('status');
  if (status === visitorStatus.chatting ||
    status === visitorStatus.transferring) {
    return <TimeCell time={visitor.get('chatBeginTime')} />;
  }
  return null;
};

const visitorInfoRendererGen = props => <VisitorInfoCell {...props} />;

const padHex = num => _.padStart(num.toString(16), 2, '0');
const segmentCellGen = (props) => {
  const ids = props.visitor.get('segmentIds'); // eslint-disable-line
  const segmentList = flow(
    map(id => _.find(props.segments, segment => segment.id === id)),
    filter(segment => segment && segment.color !== undefined),
    map(({ color, name }) => {
      const b = color % 256;
      const g = (Math.floor(color / 256) % 256);
      const r = (color - b - (256 * g)) / (256 * 256);
      return {
        color: `#${padHex(r)}${padHex(g)}${padHex(b)}`,
        name,
      };
    })
  )(ids);
  return (
    <div className={style.customerSegmentationCell}>
      {segmentList.map(seg =>
        <span
          key={seg.color}
          style={{ backgroundColor: seg.color }}
          data-tip={[
            `<span class=${style.segmentBlock} style="background-color:${seg.color}">`,
            `</span> ${_.escape(seg.name)}`,
          ].join('')}
          data-for="visitorListTooltip"
          data-html
        />
      )}
    </div>
  );
};

const config = {
  [columnType.customVariable]: {
    header: enumColumnName.CustomVariable,
    cell: customVariableCellGen,
    minWidth: 50,
  },
  [columnType.status]: {
    header: '',
    cell: statusCell,
    minWidth: 36,
  },
  [columnType.contactUserIcon]: {
    header: enumColumnName.ContactUserIcon,
    cell: () => null,
    minWidth: 50,
  },
  [columnType.name]: {
    header: enumColumnName.Name,
    cell: simpleCellGen('latestName'),
    minWidth: 50,
  },
  [columnType.email]: {
    header: enumColumnName.Email,
    cell: simpleCellGen('latestEmail'),
    minWidth: 50,
  },
  [columnType.ticketId]: {
    header: enumColumnName.TicketId,
    cell: simpleCellGen('ticketId'),
    minWidth: 50,
  },
  [columnType.department]: {
    header: enumColumnName.Department,
    cell: departmentCellGen,
    minWidth: 50,
  },
  [columnType.visits]: {
    header: enumColumnName.VisitTimes,
    cell: simpleCellGen('visitorTimes'),
    minWidth: 50,
  },
  [columnType.productService]: {
    header: enumColumnName.ProductService,
    cell: simpleCellGen('prechatProductService'),
    minWidth: 50,
  },
  [columnType.chats]: {
    header: enumColumnName.ChatTimes,
    cell: simpleCellGen('chatTimes'),
    minWidth: 50,
  },
  [columnType.currentPage]: {
    header: enumColumnName.CurrentPage,
    cell: linkCellGen('currentPage', 'currentPageTitle'),
    minWidth: 50,
  },
  [columnType.currentPageBrowsingTime]: {
    header: enumColumnName.CurrentPageBrowsingTime,
    cell: timeCellGen('currentPageEnterTime'),
    minWidth: 50,
  },
  [columnType.referrer]: {
    header: enumColumnName.Referrer,
    cell: referrerGen,
    minWidth: 50,
  },
  [columnType.searchEngine]: {
    header: enumColumnName.SearchEngine,
    cell: simpleCellGen('searchEngine'),
    minWidth: 50,
  },
  [columnType.keywords]: {
    header: enumColumnName.Keywords,
    cell: simpleCellGen('searchEngineKeywords'),
    minWidth: 50,
  },
  [columnType.autoInvitation]: {
    header: enumColumnName.AutoInvitation,
    cell: autoInvitationCellGen,
    minWidth: 50,
  },
  [columnType.campaign]: {
    header: enumColumnName.WebsiteIntegrationPlan,
    cell: campaignCellGen,
    minWidth: 50,
  },
  [columnType.landingPage]: {
    header: enumColumnName.LandingPage,
    cell: linkCellGen('landingPageUrl', 'landingPageTitle'),
    minWidth: 50,
  },
  [columnType.agents]: {
    header: enumColumnName.Operators,
    cell: agentCellGen,
    minWidth: 50,
  },
  [columnType.visitTime]: {
    header: enumColumnName.VisitTime,
    cell: dateCellGen('sessionStartTime'),
    minWidth: 50,
  },
  [columnType.waitTime]: {
    header: enumColumnName.WaitTime,
    cell: waitTimeCell,
    minWidth: 50,
  },
  [columnType.chatTime]: {
    header: enumColumnName.ChatTime,
    cell: chatTimeCell,
    minWidth: 50,
  },
  [columnType.totalTime]: {
    header: enumColumnName.TotalTime,
    cell: timeCellGen('sessionStartTime'),
    minWidth: 50,
  },
  [columnType.timezone]: {
    header: enumColumnName.TimeZone,
    cell: simpleCellGen('timezone'),
    minWidth: 50,
  },
  [columnType.city]: {
    header: enumColumnName.City,
    cell: simpleCellGen('city'),
    minWidth: 50,
  },
  [columnType.state]: {
    header: enumColumnName.StateProvince,
    cell: simpleCellGen('state'),
    minWidth: 50,
  },
  [columnType.countryRegion]: {
    header: enumColumnName.CountryRegion,
    cell: simpleCellGen('country'),
    minWidth: 50,
  },
  [columnType.numOfPages]: {
    header: enumColumnName.OfPages,
    cell: simpleCellGen('numOfPages'),
    minWidth: 50,
  },
  [columnType.visitorInfo]: {
    header: enumColumnName.Info,
    cell: visitorInfoRendererGen,
    minWidth: 260,
  },
  [columnType.visitorSegmentation]: {
    header: enumColumnName.Segment,
    cell: segmentCellGen,
    minWidth: 50,
  },
};

const outputWarning = _.once((column) => {
  console.warn(`Receive unkown column type: ${column.enumColumn}, ignored!`);
});

export function getColumn(column, key, props) {
  let renderer = () => undefined;
  let minWidth = 0;
  if (config[column.enumColumn]) {
    renderer = config[column.enumColumn].cell;
    minWidth = config[column.enumColumn].minWidth;
  } else if (process.env.NODE_ENV !== 'production') {
    outputWarning(column);
    return null;
  }
  const width = Math.max(column.width, minWidth) + 1; // border-width: 1px
  return (
    <div
      style={{
        width,
        minWidth: width,
        display: column.enumColumn === columnType.visitorInfo ? 'inline-block' : undefined,
        height: column.enumColumn === columnType.visitorInfo ? undefined : 35,
      }}
      key={key}
    >
      {renderer(props, column)}
    </div>
  );
}

export function getHeaderProps(column, customVariables) {
  if (!config[column.enumColumn]) {
    if (process.env.NODE_ENV !== 'production') {
      outputWarning(column);
    }
    return null;
  }
  let columnName = config[column.enumColumn].header;
  if (column.enumColumn === columnType.customVariable) {
    const id = column.customVariableId;
    const customVariable = _.find(customVariables, cv => cv.id === id);
    columnName = customVariable ? customVariable.name : '';
  }
  const minWidth = config[column.enumColumn].minWidth;
  const width = Math.max(column.width, minWidth);
  return { columnName, minWidth, width };
}



