import React from 'react';
import CommonStyle from '../components/CommonResources/common.css';
import NoVisitor from './NoVisitor';
import VisitorList from './VisitorList';
import SplitterVisitorDetailsTab from './SplitterVisitorDetailsTab';

const SplitterVisitorList = ({
  hasVisitors,
  visitorListShowDetails,
  onHideVisitorListDetailsClick,
}) => (
  <div className={CommonStyle.layoutContent} id="visitors" style={{ overflow: 'hidden' }}>
    {hasVisitors ? <VisitorList /> : <NoVisitor />}
    {visitorListShowDetails &&
      <div className={CommonStyle.visitorDetailsContainer}>
        <div onClick={onHideVisitorListDetailsClick} className={CommonStyle.close} />
        <div
          style={{
            width: '100%',
            height: '100%',
            position: 'relative',
          }}
        >
          <SplitterVisitorDetailsTab />
        </div>
      </div>
    }
  </div>
);

SplitterVisitorList.propTypes = {
  visitorListShowDetails: React.PropTypes.bool.isRequired,
  hasVisitors: React.PropTypes.bool.isRequired,
  onHideVisitorListDetailsClick: React.PropTypes.func.isRequired,
};

export default SplitterVisitorList;



