import React from 'react';
import Link from 'src/components/Link/Link';

class Referrer extends React.Component {
  shouldComponentUpdate() {
    return false;
  }

  render() {
    const { referrer, searchEngine, searchEngineKeywords } = this.props;
    if (
      (searchEngine && searchEngine.length > 0) ||
      (searchEngineKeywords && searchEngineKeywords.length > 0)
    ) {
      const tooltip = [
        searchEngine.length > 0 && `Search Engine: ${searchEngine}`,
        searchEngineKeywords.length > 0 && `Keywords: ${searchEngineKeywords}`,
      ].filter($ => $).join('<br>');
      return (
        <Link
          newWindow
          href={referrer}
          data-tip={tooltip}
          data-for="visitorListTooltip"
          data-multiline
        >
          {searchEngine.length > 0 && <b>{`${searchEngine}\u00A0`}</b>}
          <span>{searchEngineKeywords ? `(${searchEngineKeywords})` : ''}</span>
        </Link>
      );
    }
    return (
      <Link
        newWindow
        href={referrer}
        data-tip={`Referrer Page URL: ${referrer}`}
        data-for="visitorListTooltip"
      >
        {referrer}
      </Link>
    );
  }
}

Referrer.propTypes = {
  searchEngine: React.PropTypes.string,
  searchEngineKeywords: React.PropTypes.string,
  referrer: React.PropTypes.string,
};

export default Referrer;



