import { connect } from 'react-redux';
import * as _ from 'lodash';
import memoize from 'lru-memoize';
import VisitorList from '../components/VisitorList/VisitorList';
import * as visitorStatus from '../constants/enums/visitorStatus';
import * as selectors from '../reducers/selectors/index';
import * as actions from '../actions/ui';
import * as configActions from '../actions/config';
import * as agentActions from '../actions/agent';
import * as liveChatPermission from '../constants/enums/liveChatPermission';
import * as webActions from '../actions/web';
import * as chatListActions from '../actions/chatList';
import * as infoActions from '../actions/visitorDetails';
import * as localStorage from '../utils/localStorage';

const getWaitingAndTransferring = (function () {
  let previousList;
  let previousVisitors;
  let cacheResult;
  return (list, visitors) => {
    if (list === previousList && previousVisitors === visitors) return cacheResult;
    previousList = list;
    previousVisitors = visitors;
    const { waiting, transferring } = _.reduce(list, (result, visitorId) => {
      const status = visitors.get(visitorId).get('status');
      if (status === visitorStatus.waitingForChat) {
        result.waiting[visitorId] = visitorId; // eslint-disable-line no-param-reassign
      } else if (status === visitorStatus.transferring) {
        result.transferring[visitorId] = visitorId; // eslint-disable-line no-param-reassign
      }
      return result;
    }, { waiting: {}, transferring: {} });
    cacheResult = cacheResult ? {
      waiting: _.isEqual(waiting, cacheResult.waiting) ? cacheResult.waiting : waiting,
      transferring: _.isEqual(transferring, cacheResult.transferring) ?
        cacheResult.transferring : transferring,
    } : {
      waiting, transferring,
    };
    return cacheResult;
  };
}());

const getSelectedIndex = memoize(1)((list, selected) => _.indexOf(list, selected));

const getTotalWidth = memoize(1)(
  columnsConfig => columnsConfig.reduce((sum, config) => sum + config.width, 0) + 5
);

const mapStateToProps = (state) => {
  const visitors = selectors.getVisitors(state);
  const agentId = selectors.getCurrentAgentId(state);
  const uiConfig = selectors.getVisitorsUiConfig(state);
  const columnsConfig = selectors.getVisibleVisitorListColumns(state);
  const list = selectors.getVisibleVisitorsList(state);
  const insiteVisitorNumber = selectors.getInsiteVisitorNumber(state);
  const selectedIndex = getSelectedIndex(list, uiConfig.selected);
  const totalWidth = getTotalWidth(columnsConfig);
  const timezone = selectors.getTimezone(state);
  const timezoneOffset = selectors.getTimezoneOffset(state);
  const dateTimeFormat = selectors.getDateTimeFormat(state);
  const tabs = selectors.getVisitorTabs(state);
  return Object.assign({
    visitors,
    agentId,
    list,
    selectedIndex,
    insiteVisitorNumber,
    timezone,
    timezoneOffset,
    dateTimeFormat,
    ifCanInvite: selectors.getPermission(state, liveChatPermission.invite),
    ifCanRefuse: selectors.getPermission(state, liveChatPermission.refuseChat),
    ifCanCapture: selectors.getPermission(state, liveChatPermission.promoteVisitor),
    ifCanBan: selectors.getPermission(state, liveChatPermission.manageBan),
    ifCanJoin: selectors.getPermission(state, liveChatPermission.joinChat),
    ifCanMonitor: selectors.getPermission(state, liveChatPermission.monitorChat),
    ifEnableInvitation: selectors.getIfEnableInvitation(state),
    ifEnableTeamWork: selectors.getIfEnableTeamWork(state),
    tabs,
    columnsConfig,
    totalWidth,
    campaigns: selectors.getCampaigns(state),
    departments: selectors.getDepartments(state),
    autoInvitations: selectors.getAutoInvitations(state),
    agents: selectors.getAgents(state),
    customVariables: selectors.getCustomVariables(state),
    segments: selectors.getSegmentations(state),
    delay: selectors.getTimeDelay(state),
    customFilters: selectors.getCustomFilter(state),
    siteInfo: selectors.getSiteInfo(state),
    currentAgentId: selectors.getCurrentAgentId(state),
    ifPopupWindowForVisitors: selectors.getIfPopupWindowForVisitors(state),
  },
    uiConfig,
    // waiting and transferring are always exists in first three tabs,
    // so it's fine to directly use `list` to calculate waiting and transferring numbers
    getWaitingAndTransferring(list, visitors)
  );
};

const mapDispatchToProps = dispatch => ({
  onOrder(leftToRight, startEnumColumn, startCustomVariableId, endEnumColumn, endCustomVariableId) {
    dispatch(configActions.changeVisitorListColumnOrder(
      leftToRight,
      startEnumColumn,
      startCustomVariableId,
      endEnumColumn,
      endCustomVariableId
    ));
    dispatch(webActions.setPreference());
  },
  onErrorClose() {
    dispatch(actions.closeError());
  },
  onSort(columnType, customVariableId, order) {
    dispatch(actions.changeVisitorListOrderBy(columnType, customVariableId, order));
  },
  onTabSelect(tab) {
    dispatch(actions.changeVisitorListFilter(tab));
  },
  onResizeStop(columnType, customVariableId, width) {
    dispatch(configActions.changeVisitorListColumnSize(columnType, customVariableId, width));
    dispatch(webActions.setPreference());
  },
  onSelect(visitorId) {
    dispatch(actions.selectVisitorListRow(visitorId));
    dispatch(infoActions.resetForms());
    localStorage.deleteItem('prechatFormState');
    localStorage.deleteItem('customFields_FormState');
  },
  onAccept(visitorId, chatId, status) {
    if (status === visitorStatus.transferring) {
      dispatch(webActions.acceptTransfer(visitorId));
    } else {
      dispatch(webActions.acceptChat(visitorId));
    }
    dispatch(chatListActions.setSelected(chatId));
  },
  onRefuse(visitorId, status) {
    if (status === visitorStatus.transferring) {
      dispatch(webActions.refuseTransfer(visitorId));
    } else {
      dispatch(webActions.refuseChat(visitorId));
    }
  },
  onInvite(visitorId, message) {
    dispatch(webActions.inviteVisitorWithText(visitorId, message, (payload) => {
      /** do not jump to chat panel if manual invitation is not embedded */
      if (!payload.showChatWindow) return;
      /** select this visitor in chat panel. But should wait until visitor get updated */
      setTimeout(() => {
        dispatch(chatListActions.setSelectedViaVisitorId(payload.visitorId));
      }, 0);
    }));
  },
  onGoToChat(chatId) {
    dispatch(chatListActions.setSelected(chatId));
  },
  onJoin(visitorId, chatId) {
    dispatch(webActions.joinChat(visitorId, (payload) => {
      if (payload.errorString) return; // join failed
      setTimeout(() => {
        dispatch(chatListActions.setSelected(chatId));
      }, 0);
    }));
  },
  onMonitor(visitorId, chatId) {
    dispatch(webActions.monitorChat(visitorId, (payload) => {
      if (payload.errorString) return; // monitor failed
      setTimeout(() => {
        dispatch(chatListActions.setSelected(chatId));
      }, 0);
    }));
  },
  onCaptureClick(visitorId, shouldCapture) {
    if (shouldCapture) {
      dispatch(webActions.captureVisitor(visitorId));
    } else {
      dispatch(webActions.releaseVisitor(visitorId));
    }
  },
  onBan(visitorId, banInfo) {
    dispatch(webActions.banVisitor(visitorId, banInfo));
  },
  onDetailsClick() {
    dispatch(actions.toggleVisitorDetails());
  },
  openCustomFilterModal() {
    dispatch(actions.handleCustomFilterModal(true));
  },
  closeCustomFilterModal() {
    dispatch(actions.handleCustomFilterModal(false));
  },
  saveCustomFilters(newCustomFilter) {
    dispatch(configActions.updateCustomFilter({ customFilterSettings: newCustomFilter }));
    dispatch(agentActions.setPreference());
    dispatch(actions.handleCustomFilterModal(false));
  },
  switchSingleWindow() {
    dispatch(actions.openNewWindow());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(VisitorList);



