import React from 'react';
import * as time from 'src/utils/time';

const updateTimespan = 1 * 1e3;
class TimeCell extends React.Component {
  constructor(props) {
    super(props);
    this.timer = null;
    this.tick = this.tick.bind(this);
    this.text = this.props.time ?
      time.dateSubsToString(new Date(), this.props.time, this.props.delay) :
      '';
  }

  componentDidMount() {
    if (this.props.time) this.tick();
  }

  componentWillReceiveProps(nextProps) {
    if (!nextProps.time && this.timer) {
      clearTimeout(this.timer);
      this.timer = null;
    } else if (nextProps.time && !this.timer) {
      this.tick();
    }
  }

  shouldComponentUpdate() {
    return false;
  }

  componentWillUnmount() {
    if (this.timer) clearTimeout(this.timer);
  }

  tick() {
    this.timer = setTimeout(() => {
      if (this.props.time && this.span) {
        /**
         * Instead of using this.setState, directly manipulate DOM here.
         * This is not React way to do things, but it will be much more effecient,
         * especially when there are tons of <TimeCell /> available on viewport, where each will
         * need to update itself every second.
         */
        this.text = time.dateSubsToString(new Date(), this.props.time, this.props.delay);
        this.span.textContent = this.text;
        this.tick();
      }
    }, updateTimespan - (Date.now() % updateTimespan));
  }

  render() {
    return (
      <span style={{ color: '#666', fontSize: 15 }} ref={(span) => { this.span = span; }}>
        {this.text}
      </span>
    );
  }
}
TimeCell.propTypes = {
  time: React.PropTypes.instanceOf(Date),
  delay: React.PropTypes.number,
};

export default TimeCell;



