import axios from 'axios';
import { shell } from './electronAPI';
import * as str from '../utils/str';

function openPortalWithSession(link, controlPanelLink, siteId, email, password) {
  if (process.env.IS_WEB_APP) {
    shell.openExternal(link);
  } else {
    const autoLoginManagerUrl =
      `${controlPanelLink}/adminManage/AutoLoginManager.ashx`.replace('http://', 'https://');
    const encryptedPassword = str.passwordConvert(password);
    const loginUrl = `${controlPanelLink}/adminManage/login.aspx`;
    axios.get(
      `${autoLoginManagerUrl}?siteId=${siteId}&email=${email}&password=${encryptedPassword}`
    )
      .then((respond) => {
        shell.openExternal(
          `${loginUrl}?sessionId=${respond.data}&retUrl=${encodeURIComponent(link)}`
        );
      })
      .catch(() => {
        shell.openExternal(link);
      });
  }
}

export default openPortalWithSession;



