import { connect } from 'react-redux';
import NoVisitor from '../components/NoVisitor/NoVisitor';
import * as selectors from '../reducers/selectors/index';
import openPortalWithSession from '../services/openPortalWithSession';
import { shell } from '../services/electronAPI';
import * as actions from '../actions/ui';

const mapStateToProps = (state) => {
  function openExternalLink(link) {
    const siteId = selectors.getSiteId(state);
    const email = selectors.getCurrentAgentEmail(state);
    const password = selectors.getCurrentAgentPassword(state);
    const controlPanelUrl = selectors.getControlPanelURL(state);
    openPortalWithSession(link, controlPanelUrl, siteId, email, password);
  }
  return {
    ifPasteCode: selectors.getIfPasteCode(state),
    childWindowIsReady: selectors.childWindowIsReady(state),
    ifPopupWindowForVisitors: selectors.getIfPopupWindowForVisitors(state),
    onSimulateVisitorLinkClick() {
      shell.openExternal(selectors.getSimulateVisitorLink(state));
    },
    onInstallCodeLinkClick() {
      openExternalLink(selectors.getInstallCodeLink(state));
    },
  };
};

const mapDispatchToProps = dispatch => ({
  switchSingleWindow() {
    dispatch(actions.openNewWindow());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(NoVisitor);



