import React from 'react';
import * as _ from 'lodash';
import 'react-virtualized/styles.css';
import { List, AutoSizer } from 'react-virtualized';
import ModalBanChat from 'src/components/Modal/ModalBanChat/ModalBanChat';
import ModalConfirmation from 'src/components/Modal/Confirmation/Confirmation';
import ModalInvitation from 'src/components/Modal/Invitation/Invitation';
import ModalPopupMessage from 'src/components/Modal/PopupMessage/PopupMessage';
import * as common from 'src/utils/common';
import Row from './Row';
import style from './style.css';
import { shouldComponentUpdateGen } from '../../utils/helper';

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'VisitorListContent',
  [
    'visitors',
    'list',
    'waiting',
    'transferring',
    'selected',
    'selectedIndex',
    'columnsConfig',
    'sortColumn',
    'sortOrder',
    'totalWidth',
    'campaigns',
    'errorMessage',
  ],
  [
    'modalType',
    'visitorId',
  ]
);

const modalType = {
  none: 0,
  ban: 1,
  invitation: 2,
  confirmInvitation: 3,
  maximumInvolved: 4,
};

class VisitorListContent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modalType: modalType.none,
      visitorId: null,
    };
    this.onBan = this.onBan.bind(this);
    this.onConfirmInvitation = this.onConfirmInvitation.bind(this);
    this.onInvite = this.onInvite.bind(this);
    this.onScroll = this.onScroll.bind(this);
    this.shouldScrollToIndex = true;
    /*
     * following comment code (and some other commended code below) implement the feature
     * that will lock selected visitor in viewport when it is visible (no matter how list updated),
     * and lock scrollbar when selected visitor is not visible.
     * Though this feature is not required at the moment, code is remained as comment here
     * in case it is required in future.
     */
    // this.onRowsRendered = this.onRowsRendered.bind(this);
    // this.renderStartIndex = -1;
    // this.renderStopIndex = -1;
  }

  componentDidMount() {
    if (this.container) {
      this.container.addEventListener('scroll', this.onScroll);
    }
  }

  componentWillReceiveProps(nextProps) {
    // this.isSelectedVisible = this.renderStartIndex <= this.props.selectedIndex &&
    //   this.renderStopIndex >= this.props.selectedIndex;
    if (!this.list) return;
    if (nextProps.selected !== this.props.selected ||
    nextProps.sortColumn !== this.props.sortColumn ||
    nextProps.sortOrder !== this.props.sortOrder ||
    nextProps.transferring !== this.props.transferring ||
    nextProps.waiting !== this.props.waiting ||
    nextProps.selectedIndex !== this.props.selectedIndex) {
      /** @todo this implementation is really slow, try to calculate the correct starting index */
      this.list.recomputeRowHeights(0);
    }
    /**
     * use `selected` instead of `selectedIndex` to determine if should scroll to index, because:
     * when visitors get updated, `selectedIndex` might be changed while `selected` is unchanged.
     * according to feature, when visitors get updated, scrollbar should remain original position,
     * instead of scrolling to show selected visitor. (this is to prevent scrollbar jumping back and
     * forth when visitor list updated quickly)
     *
     * Only scroll when selected visitor is changed, i.e. selecting another visitor via keyboard or
     * mouse click.
     */
    this.shouldScrollToIndex = nextProps.selected !== this.props.selected;
  }

  shouldComponentUpdate(nextProps, nextState) {
    return shouldComponentUpdateHelper(this.props, nextProps, this.state, nextState);
  }

  componentWillUnmount() {
    if (this.container) {
      this.container.removeEventListener('scroll', this.onScroll);
    }
  }

  onBan(visitorId) {
    this.setState({ visitorId, modalType: modalType.ban });
  }

  onConfirmInvitation(visitorId) {
    this.setState({ visitorId, modalType: modalType.confirmInvitation });
  }

  onInvite(visitorId) {
    this.setState({ visitorId, modalType: modalType.invitation });
  }

  onScroll(event) {
    this.props.onHorizontalScroll(event.target.scrollLeft);
  }

  // onRowsRendered({ startIndex, stopIndex }) {
  //   this.renderStartIndex = startIndex;
  //   this.renderStopIndex = stopIndex;
  // }

  render() {
    const { visitors, list, selected, totalWidth, waiting,
      transferring, columnsConfig, selectedIndex } = this.props;
    if (columnsConfig.length === 0) return null;
    const visitorId = this.state.visitorId;
    const visitor = visitors.get(visitorId);
    const realVisitorId = visitorId ? visitor.get('realVisitorId') : -1;
    const ip = visitorId ? visitor.get('ip') : undefined;
    const codePlanId = visitorId ? visitors.get(visitorId).get('codePlanId') : undefined;
    const campaign = visitorId ?
      _.find(this.props.campaigns, c => c.id === codePlanId) : undefined;

    return (
      <AutoSizer disableWidth>
        {({ height, width }) => (
          <div
            className={style.contentContainer}
            ref={(parent) => { this.container = parent ? parent.firstChild : null; }}
          >
            <List
              height={height - 90}
              style={{ overflowX: 'auto' }}
              rowCount={
                /**
                 * You can enlarge this number here, so that more visitors will be shown.
                 */
                list.length
              }
              rowHeight={({ index }) => {
                if (selectedIndex === index) {
                  return 70;
                }
                const id = list[index];
                if (waiting[id] || transferring[id]) {
                  return 70;
                }
                return 40;
              }}
              scrollToIndex={this.shouldScrollToIndex ? selectedIndex : undefined
                /* selectedIndex >= 0 && this.isSelectedVisible ? selectedIndex : undefined */
              }
              rowRenderer={props => (
                <Row
                  {...props}
                  {...this.props}
                  width={Math.max(totalWidth, width)}
                  selectedId={selected}
                  visitor={visitors.get(list[props.index % list.length])}
                  onBan={this.onBan}
                  onConfirmInvitation={this.onConfirmInvitation}
                  onInvite={this.onInvite}
                />
              )}
              onRowRendered={undefined/* this.onRowsRendered */}
              width={width}
              ref={(element) => { this.list = element; }}
              overscanRowCount={undefined/* seems not required for optimization */}
            />
            {this.state.modalType === modalType.ban && <ModalBanChat
              ifOpen={this.state.modalType === modalType.ban}
              openModal={() => { this.setState({ modalType: modalType.ban }); }}
              closeModal={() => { this.setState({ modalType: modalType.none }); }}
              realVisitorId={realVisitorId}
              visitorId={visitorId}
              visitorIP={ip}
              onBanChat={this.props.onBan}
            />}
            {this.state.modalType === modalType.invitation && <ModalInvitation
              ifOpen={this.state.modalType === modalType.invitation}
              visitorId={this.state.visitorId}
              openModal={() => { this.setState({ modalType: modalType.invitation }); }}
              closeModal={() => { this.setState({ modalType: modalType.none }); }}
              message={
                campaign ?
                  common.replaceVisitorInfoMacros(campaign.invitationDefaultText, visitor) :
                  ''
              }
              onInvite={this.props.onInvite}
            />}
            {this.state.modalType === modalType.confirmInvitation && <ModalConfirmation
              ifOpen={this.state.modalType === modalType.confirmInvitation}
              message={[
                'This visitor has refused a previous invitation.',
                'Are you sure you want to try again?',
              ].join(' ')}
              onConfirm={() => this.setState({ modalType: modalType.invitation })}
              onCancel={() => this.setState({ modalType: modalType.none })}
            />}
            {this.props.errorMessage && this.state.modalType === modalType.maximumInvolved &&
              <ModalPopupMessage
                title="Comm100 Live Chat"
                onCancel={this.props.onErrorClose}
                message={this.props.errorMessage}
              />}
          </div>
        )}
      </AutoSizer>
    );
  }
}

export default VisitorListContent;

VisitorListContent.propTypes = {
  visitors: React.PropTypes.shape({}).isRequired,
  list: React.PropTypes.arrayOf(React.PropTypes.number).isRequired,
  waiting: React.PropTypes.shape({ }).isRequired,
  transferring: React.PropTypes.shape({ }).isRequired,
  selected: React.PropTypes.number,
  selectedIndex: React.PropTypes.number,
  onSelect: React.PropTypes.func.isRequired,
  columnsConfig: React.PropTypes.arrayOf(React.PropTypes.shape({
    enumColumn: React.PropTypes.number.isRequired,
    width: React.PropTypes.number.isRequired,
  })).isRequired,
  sortColumn: React.PropTypes.shape({
    enumColumn: React.PropTypes.number.isRequired,
    customVariableId: React.PropTypes.number.isRequired,
  }),
  sortOrder: React.PropTypes.string.isRequired,
  totalWidth: React.PropTypes.number,

  onBan: React.PropTypes.func.isRequired,
  onInvite: React.PropTypes.func.isRequired,
  onHorizontalScroll: React.PropTypes.func.isRequired,
  campaigns: React.PropTypes.arrayOf(React.PropTypes.shape({})).isRequired,

  errorMessage: React.PropTypes.string.isRequired,
  onErrorClose: React.PropTypes.func.isRequired,
};



