import React from 'react';
import Immutable from 'immutable';
import * as _ from 'lodash';
import Country from 'src/components/Icons/Country';
import Icon from 'src/components/Icon/Icon';
import Link from 'src/components/Link/Link';
import Button from 'src/components/Button/Button';

import * as visitorStatus from '../../constants/enums/visitorStatus';
import { icons } from '../../constants/enums';
import { shouldComponentUpdateGen } from '../../utils/helper';
import style from './style.css';

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'VisitorCell',
  ['visitor', 'ifSelected', 'shouldHighlight']
);

class VisitorInfoCell extends React.Component {
  shouldComponentUpdate(nextProps) {
    return shouldComponentUpdateHelper(this.props, nextProps);
  }

  render() {
    const props = this.props;
    const visitor = props.visitor;
    const agentId = props.agentId;

    const captured = visitor.get('captured');
    const status = visitor.get('status');
    const chatMonitorAgents = visitor.get('chatMonitorAgents');
    const chatActiveAgents = visitor.get('chatActiveAgents');
    const visitorId = visitor.get('id');
    const chatId = visitor.get('chatId');

    /** @todo should abstract following methods to common helper functions */

    const isAgentActiveInChat = _.indexOf(chatActiveAgents, agentId) >= 0;
    const isAgentInvolvedInMonitor = _.indexOf(chatMonitorAgents, agentId) >= 0;
    const waitingVisitorAcceptInvitationFromMe = (
      status === visitorStatus.manuallyInvitedAndWaitingVisitor &&
      isAgentActiveInChat
    );

    const showInvitation = (status === visitorStatus.inSite ||
      status === visitorStatus.chatEnded ||
      status === visitorStatus.offlineMessage ||
      status === visitorStatus.refusedByOperator ||
      status === visitorStatus.refusedByVisitor);
    const isChatting = (status === visitorStatus.chatting ||
      status === visitorStatus.transferring);

    const ifHavePermissionToChat = props.ifHavePermissionToChat;

    const showAccept = props.showAcceptButton;

    const showGoToChatButton = props.showGoToChatButton;

    const latestName = visitor.get('latestName');
    const latestEmail = visitor.get('latestEmail');

    const displayName = latestEmail === '' ? latestName : `${latestName} (${latestEmail})`;

    return (
      <div style={{ position: 'relative' }}>
        <div className={style.visitorInfoLine}>
          {visitor.get('country') !== '' &&
            <Country
              countryName={visitor.get('country')}
              countryCode={visitor.get('countryCode')}
              state={visitor.get('state')}
              city={visitor.get('city')}
              data-for="visitorListTooltip"
            />}
          <span
            className={style.visitorInfoName}
            style={{ paddingLeft: visitor.get('country') === '' ? 0 : 15 }}
            data-tip={displayName}
            data-for="visitorListTooltip"
          >
            {displayName}
          </span>
          {captured && <Icon type={icons.capture} style={{ paddingLeft: 10 }} />}
          {(props.ifSelected || props.shouldHighlight) &&
            <Link
              onClick={() => props.onDetailsClick(visitor.get('id'))}
              data-tip="View detailed information of a visitor."
              data-for="visitorListTooltip"
            >
              Details
            </Link>
          }
        </div>
        <div className={style.visitorInfoExtraLine}>
          {showAccept &&
            <Button
              text="Accept"
              size="small"
              type="primary"
              data-tip="Accept a visitor's chat request."
              data-for="visitorListTooltip"
              onClick={() => props.onAccept(visitorId, chatId, status)}
            />
          }
          {showAccept && props.ifCanRefuse &&
            <Link
              onClick={() => props.onRefuse(visitorId, status)}
              data-tip="Refuse a visitor's chat request."
              data-for="visitorListTooltip"
            >
              Refuse
            </Link>
          }
          {waitingVisitorAcceptInvitationFromMe &&
            <Button
              text="Send Message"
              size="small"
              type="primary"
              onClick={() => props.onGoToChat(chatId)}
            />
          }
          {props.ifCanInvite && props.ifEnableInvitation && showInvitation &&
            <Button
              text="Invite"
              size="small"
              type="primary"
              data-tip="Invite a visitor to chat."
              data-for="visitorListTooltip"
              onClick={() => {
                if (status === visitorStatus.refusedByVisitor) {
                  props.onConfirmInvitation(visitorId);
                } else {
                  props.onInvite(visitorId);
                }
              }}
            />
          }
          {showGoToChatButton &&
            <Button
              text="Go to Chat"
              size="small"
              type="primary"
              onClick={() => props.onGoToChat(chatId)}
            />
          }
          {isChatting &&
            (!isAgentActiveInChat && ifHavePermissionToChat && props.ifEnableTeamWork) &&
            (status !== visitorStatus.transferring || isAgentInvolvedInMonitor) &&
            (props.ifCanJoin) &&
            <Link
              onClick={() => {
                props.onJoin(visitorId, chatId);
              }}
              data-tip="Join an ongoing chat."
              data-for="visitorListTooltip"
            >
              Join
            </Link>
          }
          {isChatting &&
            (!isAgentActiveInChat && ifHavePermissionToChat && props.ifEnableTeamWork) &&
            (!isAgentInvolvedInMonitor) &&
            (props.ifCanMonitor) &&
            <Link
              onClick={() => props.onMonitor(visitorId, chatId)}
              data-tip="Invisibly monitor an ongoing chat."
              data-for="visitorListTooltip"
            >
              Monitor
            </Link>
          }
          {props.ifCanCapture &&
            <Link
              onClick={() => props.onCaptureClick(visitorId, !captured)}
              data-tip={!captured ?
                [
                  'Captured visitors will still be displayed in the visitor list after ',
                  'they leave your website.',
                ].join('') :
                [
                  'Release captured visitors so they will disappear from the visitor list ',
                  'after they leave your website.',
                ].join('')
              }
              data-for="visitorListTooltip"
            >
              {!captured ? 'Capture' : 'Release'}
            </Link>
          }
          {props.ifCanBan &&
            <Link
              onClick={() => { props.onBan(visitorId); }}
              data-tip={[
                'Ban a visitor from live chat. A banned visitor cannot initiate chats and is ',
                'not displayed in Agent Console.',
              ].join('')}
              data-for="visitorListTooltip"
            >
              Ban
            </Link>
          }
        </div>
      </div>
    );
  }
}

VisitorInfoCell.propTypes = {
  visitor: React.PropTypes.instanceOf(Immutable.Map).isRequired,
  ifSelected: React.PropTypes.bool,
  shouldHighlight: React.PropTypes.bool,
  showGoToChatButton: React.PropTypes.bool,
  onBan: React.PropTypes.func.isRequired,
  onConfirmInvitation: React.PropTypes.func.isRequired,
  onInvite: React.PropTypes.func.isRequired,
};

export default VisitorInfoCell;



