import React from 'react';
import Link from 'src/components/Link/Link';
import Tooltip from 'src/components/Tooltip/Tooltip';
import * as tooltipLabel from '../../constants/language';
import style from './NoVisitor.css';
import { isChildWindow } from '../../utils/helper';
import Icon from '../Icon/Icon';
import { icons } from '../../constants/enums';

const showPopupIcon = (isShow, cb) => {
  if (!process.env.IS_WEB_APP) {
    if (isChildWindow()) {
      return (
        <Icon
          type={icons.embet}
          size={14}
          onClick={cb}
          data-tip={tooltipLabel.tooltipPopBackInto}
          data-for="popupwindow"
          className={style.childpopupicon}
        />
      );
    } else if (!isChildWindow() && isShow) {
      return (
        <Icon
          type={icons.popup}
          size={14}
          onClick={cb}
          data-tip={tooltipLabel.tooltipPopout}
          data-for="popupwindow"
          className={style.popupicon}
        />
      );
    }
  }
  return null;
};

const NoVisitor = ({ ifPasteCode,
                      onSimulateVisitorLinkClick,
                      childWindowIsReady,
                      ifPopupWindowForVisitors,
                      switchSingleWindow,
                      onInstallCodeLinkClick }) => {
  if (isChildWindow() && !childWindowIsReady) return null;
  return (
    <div className={style.flexContainer}>
      <div className={style.container}>
        <h1>You have no visitors now.</h1>
        {!ifPasteCode && (
          <div>
            <Link onClick={onInstallCodeLinkClick}>
              Install
            </Link>
            <span>your live chat code.</span>
          </div>
        )}
        <div>
          <Link onClick={onSimulateVisitorLinkClick}>
            Simulate
          </Link>
          <span>a visitor.</span>
        </div>
      </div>
      {showPopupIcon(!ifPopupWindowForVisitors, switchSingleWindow)}
      <Tooltip
        id="popupwindow"
        effect="solid"
        place="left"
      />
    </div>
  );
};

NoVisitor.propTypes = {
  ifPasteCode: React.PropTypes.bool,
  onSimulateVisitorLinkClick: React.PropTypes.func.isRequired,
  onInstallCodeLinkClick: React.PropTypes.func.isRequired,
  childWindowIsReady: React.PropTypes.bool,
  ifPopupWindowForVisitors: React.PropTypes.bool,
  switchSingleWindow: React.PropTypes.func.isRequired,
};

export default NoVisitor;



