import React from 'react';
import {
  SortableContainer as sortableContainerGen,
  SortableElement as sortableElementGen,
} from 'react-sortable-hoc';
import Resizable from 'react-resizable-box';
import Icon from 'src/components/Icon/Icon';
import { icons } from 'src/constants/enums';
import style from './style.css';
import { getHeaderProps } from './columnConfig';
import { shouldComponentUpdateGen } from '../../utils/helper';

const Cell = ({
  columnName, width, sortOrder, onSort, onResizeStop,
  minWidth, columnType, customVariableId,
}) => {
  let component = null;
  if (columnName === '') {
    component = (
      <div
        onClick={() => onSort(columnType, customVariableId, sortOrder === 'asc' ? 'desc' : 'asc')}
        style={{ width }}
        className={style.headerStatusCell}
      >
        {sortOrder &&
          <Icon
            type={icons.arrowDown}
            style={{ transform: sortOrder === 'asc' ? 'rotateX(180deg)' : undefined }}
          />
        }
      </div>
    );
  } else {
    component = (
      <div
        onClick={() => onSort(columnType, customVariableId, sortOrder === 'asc' ? 'desc' : 'asc')}
        className={style.headerCellContent}
      >
        <span
          style={{ fontFamily: sortOrder ? 'SourceSans Pro Bold' : 'SourceSans Pro' }}
          data-tip={columnName}
          data-for="visitorListTooltip"
        >
          {columnName}
        </span>
        {sortOrder &&
          <Icon
            type={icons.arrowDown}
            style={{ transform: sortOrder === 'asc' ? 'rotateX(180deg)' : undefined }}
          />
        }
      </div>
    );
  }
  return (
    <Resizable
      width={width}
      minWidth={minWidth}
      height={33}
      isResizable={{ right: true }}
      customClass={style.headerCell}
      onResizeStop={(direction, styleObject) =>
        onResizeStop(columnType, customVariableId, styleObject.width)}
    >
      {component}
    </Resizable>
  );
};

Cell.propTypes = {
  columnName: React.PropTypes.string.isRequired,
  width: React.PropTypes.number,
  sortOrder: React.PropTypes.string,
  onSort: React.PropTypes.func,
  onResizeStop: React.PropTypes.func,
  minWidth: React.PropTypes.number,
  columnType: React.PropTypes.number.isRequired,
  customVariableId: React.PropTypes.number.isRequired,
};

const HeaderCell = sortableElementGen(Cell);

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'VisitorList - Header',
  ['columnsConfig', 'sortColumn', 'sortOrder', 'totalWidth']
);

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.updateWidth = this.updateWidth.bind(this);
  }

  componentDidMount() {
    this.updateWidth();
    this.props.getScrollLeftCallback((scrollLeft) => {
      if (!this.container) return;
      this.container.style.transform = `translateX(-${scrollLeft}px)`;
    });
    window.addEventListener('resize', this.updateWidth);
  }

  shouldComponentUpdate(nextProps) {
    return shouldComponentUpdateHelper(this.props, nextProps);
  }

  componentDidUpdate() {
    // this.updateWidth();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWidth);
  }

  updateWidth() {
    if (!this.container) return;
    const columnsConfig = this.props.columnsConfig;
    const length = columnsConfig.length;
    if (length === 0) return;
    const containerWidth = this.container.getBoundingClientRect().width;
    if (this.props.totalWidth >= containerWidth) return;
    this.props.onResizeStop(
      length - 1,
      columnsConfig[length - 1].width + (containerWidth - this.props.totalWidth)
    );
  }

  render() {
    const { columnsConfig, sortColumn, sortOrder,
      onSort, customVariables, onResizeStop } = this.props;
    if (columnsConfig.length === 0) return <div />;
    return (
      <div
        className={style.headerContainer}
        ref={(container) => { this.container = container; }}
        style={{ width: this.props.totalWidth + columnsConfig.length }}
      >
        {
          columnsConfig.map((column, index) => {
            const headerProps = getHeaderProps(column, customVariables);
            if (!headerProps) return null;
            return (
              <HeaderCell
                key={index}
                index={index}
                columnType={column.enumColumn}
                customVariableId={column.customVariableId}
                sortOrder={
                  (column.enumColumn === sortColumn.enumColumn &&
                  column.customVariableId === sortColumn.customVariableId) ?
                    sortOrder : undefined
                }
                onSort={onSort}
                onResizeStop={onResizeStop}
                {...headerProps}
              />
            );
          })
        }
      </div>
    );
  }
}

Header.propTypes = {
  columnsConfig: React.PropTypes.arrayOf(React.PropTypes.shape({
    width: React.PropTypes.number,
  })).isRequired,
  sortColumn: React.PropTypes.shape({
    enumColumn: React.PropTypes.number.isRequired,
    customVariableId: React.PropTypes.number.isRequired,
  }),
  sortOrder: React.PropTypes.oneOf(['desc', 'asc']),
  onSort: React.PropTypes.func,
  customVariables: React.PropTypes.arrayOf(React.PropTypes.shape({
    id: React.PropTypes.number,
  })),
  totalWidth: React.PropTypes.number,
  onResizeStop: React.PropTypes.func,
  scrollLeft: React.PropTypes.number,
  getScrollLeftCallback: React.PropTypes.func.isRequired,
};

export default sortableContainerGen(Header);



