import React from 'react';
import Immutable from 'immutable';
import Tooltip from 'src/components/Tooltip/Tooltip';
import MenuTab from 'src/components/MenuTab/Tab';
import LazilyLoad, { importLazy } from 'src/components/LazilyLoad/LazilyLoad';
import ModalLoading from 'src/components/Modal/ModalLoading';
import ModalReload from 'src/components/Modal/ModalReload';
import VisitorListContent from './VisitorListContent';
import Header from './Header';
import * as visitorStatus from '../../constants/enums/visitorStatus';
import * as columnType from '../../constants/enums/columnType';
import * as visitorTabType from '../../constants/enums/visitorTab';
import style from './style.css';
import { shouldComponentUpdateGen, isChildWindow } from '../../utils/helper';
import CommonStyle from '../../components/CommonResources/common.css';
import { icons } from '../../constants/enums';
import Icon from '../Icon/Icon';
import * as tooltipLabel from '../../constants/language';

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'VisitorList',
  [
    'list',
    'columnsConfig',
    'sortColumn',
    'sortOrder',
    'filter',
    'selected',
    'selectedIndex',
    'visitors',
    'waiting',
    'transferring',
    'delay',
    'ifOpenCustomFilter',
    'errorMessage',
    'ifPopupWindowForVisitors',
  ]
);

const showPopupIcon = (isShow, cb) => {
  if (!process.env.IS_WEB_APP) {
    if (isChildWindow()) {
      return (
        <Icon
          type={icons.embet}
          size={14}
          onClick={cb}
          data-tip={tooltipLabel.tooltipPopBackInto}
          data-for="popupwindow"
          className={style.popuicon}
        />
      );
    } else if (!isChildWindow() && isShow) {
      return (
        <Icon
          type={icons.popup}
          size={14}
          onClick={cb}
          data-tip={tooltipLabel.tooltipPopout}
          data-for="popupwindow"
          className={style.popuicon}
        />
      );
    }
  }
  return null;
};

class VisitorList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      VisitorListScrollLeft: 0,
    };
    this.onHorizontalScroll = this.onHorizontalScroll.bind(this);
  }
  componentDidMount() {
    // force tooltip re-render to be after children mount
    if (this.timer) {
      clearTimeout(this.timer);
    }
    this.timer = setTimeout(() => Tooltip.rebuild(), 0);
  }

  shouldComponentUpdate(nextProps, nextState) {
    return shouldComponentUpdateHelper(this.props, nextProps) ||
      this.state.VisitorListScrollLeft !== nextState.VisitorListScrollLeft;
  }

  componentDidUpdate() {
    if (this.timer) {
      clearTimeout(this.timer);
    }
    this.timer = setTimeout(() => Tooltip.rebuild(), 0);
  }

  componentWillUnmount() {
    if (this.timer !== null) {
      clearTimeout(this.timer);
      this.timer = null;
    }
  }
  onHorizontalScroll(scrollLeft) {
    this.onScrollLeft(scrollLeft);
  }
  render() {
    const props = this.props;
    const columnsConfig = props.columnsConfig;
    const waitingNumber = Object.keys(props.waiting).length;
    const shouldShowNoFilter =
      props.filter === visitorTabType.customized &&
      (!props.customFilters || props.customFilters.length === 0);
    return (
      <div style={{ height: '100%' }}>
        <div className={style.top}>
          <div className={style.topInfo}>
            <div className={style.topInfoLine}>
              <span>{props.insiteVisitorNumber}</span>
              {`Visitor${props.insiteVisitorNumber > 1 ? 's' : ''} in Site`}
            </div>
            <div className={style.topInfoLine}>
              <span>{waitingNumber}</span>
              {`Visitor${waitingNumber > 1 ? 's' : ''} Waiting for Chat`}
            </div>
          </div>
          <div className={CommonStyle.topTab}>
            <MenuTab
              tabs={props.tabs}
              selectedTab={props.filter}
              onSelect={props.onTabSelect}
              onIconClick={props.openCustomFilterModal}
              ifEqualWidth={false}
              className={style.visitorstopmenu}
            />
            {showPopupIcon(!props.ifPopupWindowForVisitors, props.switchSingleWindow)}
            <Tooltip
              id="popupwindow"
              effect="solid"
              place="left"
            />
          </div>
        </div>
        <Header
          columnsConfig={props.columnsConfig}
          axis="x"
          lockAxis="x"
          distance={5}
          shouldCancelStart={(e) => {
            if (e.target.parentNode.children[0] === e.target) return false;
            return true;
          }}
          sortColumn={props.sortColumn}
          sortOrder={props.sortOrder}
          onSort={props.onSort}
          onResizeStop={props.onResizeStop}
          onSortEnd={({ oldIndex, newIndex }) => {
            const columnConfigInStartPoint = columnsConfig[oldIndex];
            const columnConfigInEndPoint = columnsConfig[newIndex];
            props.onOrder(
              newIndex > oldIndex,
              columnConfigInStartPoint.enumColumn,
              columnConfigInStartPoint.customVariableId,
              columnConfigInEndPoint.enumColumn,
              columnConfigInEndPoint.customVariableId
            );
          }}
          helperClass={style.dragging}
          customVariables={props.customVariables}
          totalWidth={props.totalWidth}
          scrollLeft={this.state.VisitorListScrollLeft}
          getScrollLeftCallback={(callback) => {
            this.onScrollLeft = callback;
          }}
        />
        {
          shouldShowNoFilter && (
            <div className={style.nofilterDiv}>
              <h1>{'You haven\'t set up custom filter conditions yet.'}</h1>
              <h1>{'Please click the Settings icon next to "Custom Filter" to add conditions first.'}</h1>
              <div className={style.nofilterImgWrapper} />
            </div>
          )
        }
        {
          !shouldShowNoFilter && (
          <VisitorListContent
            {...props}
            totalWidth={props.totalWidth}
            onHorizontalScroll={this.onHorizontalScroll}
          />
          )
        }
        <Tooltip
          id="visitorListTooltip"
          effect="solid"
          place="bottom"
        />
        {props.ifOpenCustomFilter && (
          <LazilyLoad
            modules={{ CustomFilterModal: () => importLazy(new Promise((resolve, reject) => {
              require.ensure(['src/components/Modal/CustomFilter/CustomFilterModal'], (require) => {
                resolve(require('src/components/Modal/CustomFilter/CustomFilterModal'));
              }, 'custom-filter').catch(reject);
            })) }}
            loading={<ModalLoading ifOpen />}
            retry={ModalReload}
          >
            {
              ({ CustomFilterModal }) => (
                props.ifOpenCustomFilter && <CustomFilterModal
                  siteInfo={props.siteInfo}
                  campaigns={props.campaigns}
                  departments={props.departments}
                  autoInvitations={props.autoInvitations}
                  agents={props.agents.filter(agent => agent.id !== props.currentAgentId)}
                  customVariables={props.customVariables}
                  segments={props.segments}
                  customFilter={props.customFilters}
                  closeModal={props.closeCustomFilterModal}
                  saveCustomFilters={props.saveCustomFilters}
                  currentAgentId={props.currentAgentId}
                />
              )
            }
          </LazilyLoad>
        )}
      </div>
    );
  }
}

VisitorList.propTypes = {
  columnsConfig: React.PropTypes.arrayOf(React.PropTypes.shape({})),
  sortColumn: React.PropTypes.shape({
    enumColumn: React.PropTypes.number.isRequired,
    customVariableId: React.PropTypes.number.isRequired,
  }),
  sortOrder: React.PropTypes.string,
  onSort: React.PropTypes.func,
  onResizeStop: React.PropTypes.func,
  onOrder: React.PropTypes.func,
  tabs: React.PropTypes.arrayOf(React.PropTypes.shape({
    name: React.PropTypes.string.isRequired,
    type: React.PropTypes.number.isRequired,
  })).isRequired,
  filter: React.PropTypes.number.isRequired,
  selected: React.PropTypes.number,
  onTabSelect: React.PropTypes.func.isRequired,
  onSelect: React.PropTypes.func.isRequired,
  list: React.PropTypes.arrayOf(React.PropTypes.number).isRequired,
  waiting: React.PropTypes.shape({ }).isRequired,
  insiteVisitorNumber: React.PropTypes.number.isRequired,
  totalWidth: React.PropTypes.number,
  ifPopupWindowForVisitors: React.PropTypes.bool,
  switchSingleWindow: React.PropTypes.func.isRequired,
};

export default VisitorList;

VisitorList.examples = {
  example: {
    description: 'hello wolrd',
    component: (
      React.createElement(
        // eslint-disable-next-line
      class extends React.Component {
        constructor() {
          super();
          this.state = {
            visitors: Array.from({ length: 1e2 })
              .reduce((obj, x, i) => obj.set(i, Immutable.Map({
                id: i,
                status: visitorStatus[Object.keys(visitorStatus)[i % 14]],
                latestName: `name_${i}`,
                referrer: `https://www.google.com?id=${i}`,
                numOfPages: Math.floor((Math.random() * 10) + 5),
                landingPageUrl: `https://www.landing-page.com?id=${i}`,
                landingPageTitle: `Landing Page - ${i}`,
                chatBeginTime: new Date(2016, 11, 7),
                visitorTimes: new Date(2016, 11, 7),
                countryCode: 'CN',
                country: 'China',
                city: 'Hangzhou',
                state: 'Zhejiang',
              })), Immutable.Map({ })),
            list: Array.from({ length: 1e2 }).map((x, i) => i),
            selected: 12,
            waiting: [1, 2],
            sortColumn: 2,
            columns: [
              { name: '', type: columnType.status, width: 36 },
              { name: 'Visitor Info', type: columnType.visitorInfo, width: 200 },
              { name: 'Landing Page', type: columnType.landingPage, width: 130 },
              { name: '# of Pages', type: columnType.numOfPages, width: 80 },
              { name: 'Chat Time', type: columnType.chatTime, width: 130 },
              { name: 'Visit Time', type: columnType.visitTime, width: 130 },
            ],
            tabs: [
              { name: 'All Visitors', type: visitorTabType.allVisitors },
              { name: 'All Chats', type: visitorTabType.allChats },
              { name: 'My Chats', type: visitorTabType.myChats },
              { name: 'Customzied', type: visitorTabType.customized },
            ],
            selectedTab: visitorTabType.allVisitors,
          };
          this.onOrder = this.onOrder.bind(this);
          this.onSort = this.onSort.bind(this);
          this.onTabSelect = this.onTabSelect.bind(this);
          this.onSelect = this.onselect.bind(this);
        }

        onOrder({ oldIndex, newIndex }) {
          this.setState({
            columns: this.state.columns.map((column, index, list) => {
              if (index !== oldIndex && index !== newIndex) return column;
              if (index === oldIndex) return list[newIndex];
              return list[oldIndex];
            }),
          });
        }

        onSelect(index) {
          this.setState({ selected: index });
        }

        onSort(index) {
          this.setState({ sortColumn: index });
        }

        onTabSelect(tab) {
          this.setState({ selectedTab: tab });
        }

        render() {
          return (
            <VisitorList
              {...this.state}
              sortOrder="desc"
              onSort={this.onSort}
              onOrder={this.onOrder}
              onTabSelect={this.onTabSelect}
            />
          );
        }
      })
    ),
  },
};




