import React from 'react';
import Immutable from 'immutable';
import * as _ from 'lodash';
import classnames from 'classnames';

import Tooltip from 'src/components/Tooltip/Tooltip';

import style from './style.css';
import { getColumn } from './columnConfig';

import { shouldComponentUpdateGen } from '../../utils/helper';
import * as commonHelper from '../../utils/common';
import * as visitorAudioVideoChatStatus from '../../constants/enums/visitorAudioVideoChatStatus';

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'Row',
  [
    'visitor', 'index', 'columnsConfig', 'selected',
    'waiting', 'transferring', 'delay', 'audioVideoChatStatus',
  ]
);

class Row extends React.PureComponent {
  shouldComponentUpdate(nextProps) {
    /** @todo `selected` change might cause unnecessary render */
    return shouldComponentUpdateHelper(this.props, nextProps) ||
      this.props.style.height !== nextProps.style.height ||
      this.props.style.top !== nextProps.style.top;
  }

  componentWillUnmount() {
    /**
     * This is to fix issue 18117.
     *
     * @todo
     *
     * Not a good solution, as it will hide ALL tooltips when one row is removed, including tooltip
     * that is outside of <VisitorList />.
     *
     * Better to include <Tooltip /> in <Row />, however that might cause tooltip to be in wrong
     * location (could be a CSS bug).
     *
     * Also, could pass a target in hide() function to only hide certain tooltip. However, that
     * requires too much work. Will do it later.
     */
    Tooltip.hide();
  }

  render() {
    const props = this.props;
    const { visitor } = props;

    const id = visitor.get('id');
    const status = visitor.get('status');
    const ifSelected = id === props.selected;
    const shouldHighlight = !!(props.waiting[id] || props.transferring[id]);
    const showGoToChatButton = commonHelper.showGoToChatButton(visitor, props.agentId);
    const departmentId = visitor.get('departmentId');
    const department = _.find(props.departments, dep => dep.id === departmentId);
    const chatInvolvedAgents = visitor.get('chatInvolvedAgents');
    const ifHavePermissionToChat = commonHelper.isHavePermissionDealChat(
      status,
      chatInvolvedAgents,
      department,
      props.agentId
    );
    const showAcceptButton = commonHelper.showAcceptButton(
      visitor,
      department,
      props.agentId
    );

    const avStatus = visitor.get('audioVideoChatStatus');
    let backgroundColor;
    if (shouldHighlight
      || avStatus === visitorAudioVideoChatStatus.visitorRequestAudioChat
      || avStatus === visitorAudioVideoChatStatus.visitorRequestVideoChat) {
      backgroundColor = '#fedb9e';
    }
    if (ifSelected) backgroundColor = '#cce7f5';

    const passedThruProps = Object.assign(
      { shouldHighlight, ifSelected, showGoToChatButton, ifHavePermissionToChat, showAcceptButton },
      props
    );

    let onDoubleClick;
    if (showGoToChatButton) {
      onDoubleClick = () => { props.onGoToChat(visitor.get('chatId')); };
    } else if (showAcceptButton) {
      onDoubleClick = () => { props.onAccept(id, visitor.get('chatId'), status); };
    }

    return (
      <div
        style={shouldHighlight ?
        {
          ...props.style,
          backgroundColor,
            // border-width: 1px for each cell need to be included
          width: props.width + props.columnsConfig.length,
          borderBottom: 1,
          borderBottomColor: '#feedce',
          borderBottomStyle: 'solid',
        } :
          { ...props.style, backgroundColor, width: props.width + props.columnsConfig.length }}
        className={classnames(style.columnRow, props.index % 2 ? style.odd : style.even)}
        onClick={ifSelected ? undefined : () => { props.onSelect(visitor.get('id')); }}
        onDoubleClick={onDoubleClick}
        key={visitor.get('id')}
      >
        {
          props.columnsConfig.map((config, i) => getColumn(config, i, passedThruProps))
        }
      </div>
    );
  }
}

Row.propTypes = {
  visitor: React.PropTypes.instanceOf(Immutable.Map).isRequired,
  selected: React.PropTypes.number,
  waiting: React.PropTypes.shape({}).isRequired,
  transferring: React.PropTypes.shape({}).isRequired,
  index: React.PropTypes.number.isRequired,
  style: React.PropTypes.shape({
    height: React.PropTypes.number.isRequired,
    top: React.PropTypes.number.isRequired,
  }).isRequired,
  columnsConfig: React.PropTypes.arrayOf(React.PropTypes.shape({})).isRequired,

  onSelect: React.PropTypes.func.isRequired,
  onGoToChat: React.PropTypes.func.isRequired,
  width: React.PropTypes.number.isRequired,
};

export default Row;



