import React from 'react';
import Modal from '../Modal';
import TextArea from '../../TextArea/TextArea';

class ModalInvitation extends React.Component {
  constructor(props) {
    super(props);
    this.state = { message: props.message };
    this.handleTextAreaChange = this.handleTextAreaChange.bind(this);
    this.handleInvite = this.handleInvite.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    if (this.props.message === nextProps.message) return;
    this.setState({ message: nextProps.message });
  }
  handleTextAreaChange(event) {
    this.setState({
      message: event.target.value,
    });
  }
  handleInvite() {
    this.props.onInvite(this.props.visitorId, this.state.message);
    this.props.closeModal();
  }
  render() {
    return (
      <Modal
        closeModal={this.props.closeModal}
        title="Enter an Invitation Message"
        actions={{
          okProps: { text: 'OK', eventClick: this.handleInvite },
          cancelProps: { text: 'Cancel' },
        }}
      >
        <TextArea
          meta={{}}
          input={{
            onChange: this.handleTextAreaChange,
            value: this.state.message,
          }}
          cols="40"
          rows="5"
        />
      </Modal>
    );
  }

}

export default ModalInvitation;

ModalInvitation.propTypes = {
  ifOpen: React.PropTypes.bool.isRequired,
  onInvite: React.PropTypes.func.isRequired,
  closeModal: React.PropTypes.func.isRequired,
  visitorId: React.PropTypes.number,
  message: React.PropTypes.string.isRequired,
};




